@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');


.main-title {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  
  color: rgba(0, 0, 0, 0.7);
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
}

.input-type {
  display: none;
}

.image {
  width: 100px;
  height: 78.75px;
}

.block-type {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 280px;
  height: 362px;
  background: #FAFAFA;
  border:1px dashed rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  position: relative;
}



.input-text {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  text-align: center;

  color: #A6A6A6;
}

.footer-type {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  color: #A3A3A3;
}

.image-not-upload {
  display: none;
}

.image-upload {
  display: flex;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.circlde-progress {
  display: flex;
  position: absolute;
}

.percent {
  display: flex;
  flex-direction: column;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  justify-content: center;
  color: #FBFBFB;
  position: absolute;
  top: 210px;
}

.button-select {
  color: #000000;
  text-decoration: underline;

}

.button-select:hover {
  cursor: pointer;
}

.demo-image {
  display: flex;
  width: 100%;
  height: 100%;
  filter: blur(1px);
  border-radius: 10px;
}